<template>
<div>
<van-picker
  title=""
  show-toolbar
  :columns="columns"
  @confirm="onConfirm"
  @cancel="$emit('close')"
  :default-index="localGender"
/>
</div>
</template>

<script>
import { updateUserProfileAPI } from '../../../api/index.js'
export default {
  name: 'UpdateGender',
  props: {
    value: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      localGender: this.value,
      columns: ['', '']
    }
  },
  methods: {
    async onConfirm (item, index) {
      /* value index */
      // 
      this.$toast.loading({
        message: '...',
        forbidClick: true, // 
        duration: 0 //  toast
      })
      try {
        await updateUserProfileAPI({
          gender: index
        })
        // 
        this.$emit('input', index)
        // 
        this.$emit('close')
        this.$toast.success('')
      } catch (error) {
        this.$toast.fail('')
      }
    }
  }
}
</script>

<style lang="less" scoped>

</style>
