<template>
  <div class="update-photo">
   <img :src="value" class="img" ref="img">
    <div class='toolbar'>
       <span class="cancel" @click="$emit('close')"></span>
       <span class="confirm" @click="onConfirm"></span>
    </div>
  </div>
</template>

<script>
import { updateUserphotoAPI } from '../../../api/index.js'
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
export default {
  name: 'UpdatePhoto',
  props: {
    // ObjectObjectdefaultnull
    value: {
      type: String, // blob
      default: ''
    }
  },
  data () {
    return {
      cropper: null
    }
  },
  mounted () {
    // 
    this.initCropper()
  },
  methods: {
    initCropper () {
      // 1. dom
      const IMAGE = this.$refs.img
      // 2. 
      this.cropper = new Cropper(IMAGE, {
        //  config
        viewMode: 1, // 01,2,3
        dragMode: 'move', //  move
        aspectRatio: 1, //  11:1
        cropBoxMovable: false, // 
        cropBoxResizable: false, // 
        background: false, // 
        movable: true //  true
        /* crop (event) { // 
          const eventObj = {
            x: event.detail.x,
            y: event.detail.y,
            width: event.detail.width,
            height: event.detail.height,
            rotate: event.detail.rotate,
            scaleX: event.detail.scaleX,
            scaleY: event.detail.scaleY
          }
          console.log(eventObj)
        } */
      })
    },
    onConfirm () {
      // 1 
      // 1.
      // this.cropper.getData()
      // 2.

      // 2
      // 1. this.cropper.getCroppedCanvas().toBlob() 
      this.cropper.getCroppedCanvas().toBlob((blob) => {
        // 2.blob
        this.updateUserphoto(blob)
      })
    },
    async updateUserphoto (blob) {
      // 
      this.$toast.loading({
        message: '...',
        forbidClick: true, // 
        duration: 0 //  toast
      })
      // 3.FormData
      const formData = new FormData()
      // 4. blobformDataformData.append('key','value')
      formData.append('photo', blob)
      try {
        const { data: res } = await updateUserphotoAPI(formData)
        // photo
        this.$emit('update-photo', res.data.photo)
        // 
        this.$toast.success('')
        // 
        this.$emit('close')
      } catch (error) {
        // 
        this.$toast.fail('')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.update-photo{
  background-color: #001;
  height: 100%;
  .toolbar{
   position: fixed;
   right:0;
   left:0;
   bottom:0;
   display: flex;
   justify-content: space-between;
  color:#fff;
   .cancel,.confirm{
       font-size: 40px;
       margin: 12px;
      }
   }
   .img{
   display: block; // 
   max-width: 100%; // 
   }
}
</style>
