<template>
<div class="updata-name">
<van-nav-bar
title=""
left-text=""
right-text=""
@click-left="$emit('close')"
@click-right="onConfirm"
/>
<!--  -->
 <div class="updata-name-text">
  <van-field
  v-model.trim="localName"
  rows="2"
  autosize
  type="textarea"
  maxlength="10"
  placeholder=""
  show-word-limit
/>
 </div>
</div>
</template>

<script>
import { updateUserProfileAPI } from '../../../api/index.js'
export default {
  name: 'UpdataName',
  props: { // v-model$emit(input)
    value: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      localName: this.value // 
    }
  },
  methods: {
    // 
    async onConfirm () {
      // 
      this.$toast.loading({
        message: '...',
        forbidClick: true, // 
        duration: 0 //  toast
      })
      try {
        if (!this.localName.length) {
          this.$toast('')
          return
        }
        await updateUserProfileAPI({
          name: this.localName
        })
        // 
        this.$emit('input', this.localName)
        // 
        this.$emit('close')
        this.$toast.success('')
      } catch (error) {
        // 409
        if (error.response && error.response.status === 409) {
          this.$toast.fail('')
        } else {
          this.$toast.fail('')
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
.updata-name{
  .van-nav-bar {
    background-color: #fff;
      :deep(.van-nav-bar__title) {
        color: #000;
       }
      .van-icon{
        color:#3296fa;
       }
    }
      .updata-name-text{
        padding: 20px;
      }
}
</style>
