<template>
<div class="user-profile">
   <van-nav-bar title="" left-arrow @click-left="$router.back()" fixed />
   <div class="main-wrap">
    <van-cell-group>
  <van-cell title="" is-link @click="$refs['file'].click()">
    <template #default>
      <van-image class="avatar" round fit="cover"
        :src="user.photo"/>
    </template>
  </van-cell>
  <van-cell title="" is-link :value="user.name" @click="isUpdataNameShow=true" />
  <van-cell title="" is-link :value="user.gender === 0 ? '': ''" @click="isUpdataGenderShow=true" />
  <van-cell title="" is-link :value="user.birthday" @click="isUpdataBirthdayShow=true"  />
  <!-- /
   -->
</van-cell-group>
   </div>

   <!--  -->
   <van-popup v-model="isUpdataNameShow"
   position="bottom" :style="{ height: '100%' }">
   <UpdataName @close="isUpdataNameShow=false" v-model="user.name"></UpdataName>
   </van-popup>

      <!--  -->
   <van-popup v-model="isUpdataGenderShow"
   position="bottom" :style="{ height: '40%' }">
     <UpdataGender @close="isUpdataGenderShow=false" v-model="user.gender"></UpdataGender>
   </van-popup>

   <!--  -->
   <van-popup v-model="isUpdataBirthdayShow"
   position="bottom"  :style="{ height: '100%' }">
     <UpdataBirthday @close="isUpdataBirthdayShow=false" v-model="user.birthday"
    ></UpdataBirthday>
   </van-popup>

   <!--  -->
   <!--  ref$refs-->
   <input type="file" hidden ref="file" @change="onFileChange" />
   <!--  -->
   <van-popup v-model="isUpdatePhotoShow" :style="{ height: '100%' }"
   position="bottom">
     <UpdatePhoto v-if="isUpdatePhotoShow" @close="isUpdatePhotoShow=false" v-model="image"
      @update-photo="user.photo=$event"></UpdatePhoto>
   </van-popup>
</div>
</template>

<script>
import UpdataName from './components/updat-name.vue'
import UpdataGender from './components/update-gender.vue'
import UpdataBirthday from './components/update-birthday.vue'
import UpdatePhoto from './components/update-photo.vue'
import { getUserProfileAPI } from '../../api/index.js'
export default {
  name: 'UserProfile',
  components: {
    UpdataName,
    UpdataGender,
    UpdataBirthday,
    UpdatePhoto
  },
  data () {
    return {
      user: {}, // 
      // user.gender 0 1
      isUpdataNameShow: false, // 
      isUpdataGenderShow: false, // 
      isUpdataBirthdayShow: false, // 
      isUpdatePhotoShow: false, // 
      image: null // 
    }
  },
  methods: {
    async loadProfile () {
      try {
        const { data: res } = await getUserProfileAPI()
        this.user = res.data
      } catch (error) {
        this.$toast('')
      }
    },
    // 
    onFileChange () {
      // 1.
      const file = this.$refs.file.files[0]
      // 2. blob
      this.image = window.URL.createObjectURL(file)
      // 3.value
      this.$refs.file.value = ''
      // 4.
      this.isUpdatePhotoShow = true
    }
  },
  created () {
    // 
    this.loadProfile()
  }

}
</script>

<style lang="less" scoped>
.user-profile{
  .main-wrap{
    padding-top:94px;
  }
  .avatar{
    width: 52px;
    height: 52px;
  }
  .van-popup{
    background-color: #f2f2f2;
  }
}
</style>
