<template>
<div class="updata-birthday">
  <van-datetime-picker
  type="date"
  title=""
  :min-date="minDate"
  :max-date="maxDate"
  @confirm="onConfirm"
  @cancel="$emit('close')"
  v-model="localBirthday"
/>
</div>
</template>

<script>
import { updateUserProfileAPI } from '../../../api/index.js'
import dayjs from 'dayjs'
export default {
  name: 'UpdateBirthday',
  props: {
    value: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      localBirthday: this.value, // 
      minDate: new Date(1917, 0, 1), // 
      maxDate: new Date() // 
    }
  },
  methods: {
    // 
    async onConfirm (value) {
      // 
      this.$toast.loading({
        message: '...',
        forbidClick: true, // 
        duration: 0 //  toast
      })
      const newlocalBirthday = dayjs(value).format('YYYY-MM-DD') // 
      try {
        await updateUserProfileAPI({
          birthday: newlocalBirthday
        })
        // 
        this.$emit('input', newlocalBirthday)
        // 
        this.$emit('close')
        this.$toast.success('')
      } catch (error) {
        this.$toast.fail('')
      }
    }
  }
}
</script>

<style lang="less" scoped>

</style>
